import { Storage } from "@ionic/storage";
import { Component } from "@angular/core";
import { NavController, LoadingController } from "ionic-angular";
import { Mixpanel } from "@ionic-native/mixpanel";

declare var PaystackPop: any;


@Component({
  selector: "page-give-modal",
  templateUrl: "give-modal.html"
})
export class GiveModalPage {
  name: string;
  email: string;
  phone: number;
  amount: number;
  type: string;
  selectOptions = {
    mode: "ios"
  };

  constructor(
    public navCtrl: NavController,
    public loadingCtrl: LoadingController,
    public storage: Storage,
    public mixpanel: Mixpanel
  ) {
    this.storage.get("user").then(user => {
      if (user !== null) {
        this.name = user.name;
        this.email = user.email;
        this.phone = user.phone;
      }
    });
    this.mixpanel.track("Give Intent Start");
  }

  randString(x) {
    let s = "";
    while (s.length < x && x > 0) {
      var r = Math.random();
      s +=
        r < 0.1
          ? Math.floor(r * 100)
          : String.fromCharCode(Math.floor(r * 26) + (r > 0.5 ? 97 : 65));
    }
    let n = s + "" + Math.floor(Math.random() * 1000000000);
    return n;
  }

  payWithPaystack() {
    this.sendMixPanel();
    var handler = PaystackPop.setup({
      key: "pk_live_e6f903a4514539337433c603cfe91827ddab8f8d",
      email: this.email,
      amount: this.amount * 100,
      ref: this.randString(10),
      metadata: {
        custom_fields: [
          {
            display_name: "Name",
            variable_name: "payee_name",
            value: this.name
          },
          {
            display_name: "Phone",
            variable_name: "payee_phone",
            value: this.phone
          },
          {
            display_name: "Payment Type",
            variable_name: "payment_type",
            value: this.type
          }
        ]
      },
      callback: response => {
        this.paymentMade(response);
      },
      onClose: function() {}
    });
    handler.openIframe();
  }

  paymentMade(response) {
    let gave = {
      name: this.name,
      email: this.email,
      phone: this.phone,
      type: this.type,
      amount: this.amount,
      ref: response.trxref
    };
    this.navCtrl.setRoot("PaymentMadePage", { details: gave });
  }

  sendMixPanel = () => {
    this.mixpanel.track("Give Intent Finished");
  };
}



